import React, { useState } from 'react';
import PlayerPropListData from "../data/PlayerPropListData";
import { ListItem, ListItemIcon, ListItemText } from '@mui/material';
import styles from "./PropBetListItem.module.css";
import BetButtonGroup from './BetButtonGroup';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

interface Props {
    id : number;
    collapsedData : PlayerPropListData;
}

const PropBetListItem: React.FC<Props> = (props) => {
    const [expanded, setExpanded] = useState<boolean>(false);

        return (
            <div className={styles.container}>
              <ListItem key={props.id}>
                <ListItemIcon onClick={() => setExpanded(true)}>
                    <ExpandMoreIcon></ExpandMoreIcon>
                </ListItemIcon>
                <div className={styles.listInfo}>
                    <ListItemText primary={props.collapsedData.name}></ListItemText>
                    <ListItemText primary={'-'}></ListItemText>
                    <ListItemText primary={props.collapsedData.type}></ListItemText>
                </div>
                <div className={styles.lineDiv}>
                    <ListItemText className={styles.lineItem} primary={props.collapsedData.line}></ListItemText>
                    <BetButtonGroup id={props.id}overPct={props.collapsedData.overPct} underPct={props.collapsedData.underPct}></BetButtonGroup>
                </div>
                </ListItem>
            </div>
          );
};

export default PropBetListItem;
  
  